export default {
	data() {
		return {
			/* 日期时间对象 */
			year:null,
			month:null,
			day:null,
			hour:null,
			minute:null,
			week:null
		}
	},
	onShow:function(){
		var _this=this;
		_this.getTheDateTime();			  /* 初始化先执行一次时间获取方法 */
		setInterval(function(){			/* 一秒执行一次时间获取方法 */
			_this.getTheDateTime();
		},1000)
	},
	methods: {
		getTheDateTime:function(){       /* 获取时间日期的方法 */
			var dt = new Date();
			this.year=dt.getFullYear();
			this.month=dt.getMonth()+1;
			this.day=dt.getDate();
			this.hour=dt.getHours();
			if(dt.getMinutes()>=0&&dt.getMinutes()<=9){
				this.minute="0"+dt.getMinutes();       /* 0分到9分前面加0字符 */
			}else{
				this.minute=dt.getMinutes();
			};
			/* 处理星期,转为汉字 */
			if(dt.getDay()==0){
				this.week="日";
			}else if(dt.getDay()==1){
				this.week="一";
			}else if(dt.getDay()==2){
				this.week="二";
			}else if(dt.getDay()==3){
				this.week="三";
			}else if(dt.getDay()==4){
				this.week="四";
			}else if(dt.getDay()==5){
				this.week="五";
			}else if(dt.getDay()==6){
				this.week="六";
			}
		},
		gotomf:function(){
			uni.navigateTo({
				url:"../monthFlower/monthFlower"
			})
		}
	}
}
